import { Component , ViewChild} from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';


import {MobileAppSystemP2l} from '../../../providers/mobile.app.system.p2l'
import {User} from '../../../providers/user'
import {ModalService} from '../../../providers/modal.service'
import {AlertService} from '../../../providers/alert.service'

import { Keyboard } from '@ionic-native/keyboard';
import { CustomKeyBoard } from '../../../components/customKeyBoard/custom-keyboard';

/**
 * Generated class for the PlacInTotePage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

export class P2lJob{
  jobID:number;
  numProducts:number;
  numCartons:number;
  highPriority:string;
}



@IonicPage()
@Component({
  selector: 'page-p2l-outstanding',
  templateUrl: 'p2l-outstanding.html',
})
export class P2lOutstandingPage {

  jobList: P2lJob[]=[];
  selectedJobID:number = 0;

  constructor(public navCtrl: NavController, public navParams: NavParams,
  			  public mobileAppSystem:MobileAppSystemP2l,
          private keyboard:Keyboard,
  	    	private modalService:ModalService,
  	    	private alertService:AlertService, 
  			  public user:User) {
   
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad P2L Outstanding Jobs page');

    this.onRefreshJobList();
    // this.jobList = [ 
    //   {jobID:1,numProducts:3,numCartons:3,highPriority:true}, 
    //   {jobID:2,numProducts:4,numCartons:5,highPriority:false}, 
    //   {jobID:3,numProducts:5,numCartons:8,highPriority:false}, 
    //   {jobID:4,numProducts:9,numCartons:7,highPriority:true}, 
    // ];
  }

  onRefreshJobList(){
    let svc = this;
    this.mobileAppSystem.getP2LJobs(this.user.sessionInfo.userWarehouse, function(res:any){
       if(res!=null)
         svc.jobList  = res.result.productList;
    });    
  }
  
  onSelectJob(job:P2lJob)
  {
    //console.log('onSelectJob=>' + job.jobID);
    this.selectedJobID = job.jobID;
  }


  openPage() {

    if(this.selectedJobID==0)
      return;

    let svc = this;
    this.mobileAppSystem.getNextProduct(this.selectedJobID, function(res:any){
      if(res==null)return;
      if(res.isError)
      {
        svc.alertService.doAlert('Error', res.result.statusMsg, 'OK');
        return;
      }
      else
      {
        svc.user.p2lJobID = svc.selectedJobID;
        svc.navCtrl.push('P2lScanProductPage');
      }
    });    
  }

  

}



